@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  width: 100%;
  height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  color: black;
  margin: 0;
  scroll-behavior: smooth;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

.hide {
  display: none;
}

figure {
  width: 100%;
  height: 100%;
  display: block;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  background: #fff1e1;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.about-me {
  width: 100%;
  max-width: 1440px;
  text-align: center;
  padding: 50px;
  margin: 0 auto;
}

.about-me__title {
  margin: 0 0 20px;
}

.about-me__text {
  margin: 0 0 20px;
}

.about-me__list {
  text-align: start;
  display: flex;
  flex-flow: row wrap;
  gap: 20px 0;
  margin: 0;
}

.contacts {
  display: flex;
  flex-flow: column;
}

.contacts__link {
  text-decoration: none;
  font-weight: 700;
  color: #B6452C;
  margin: 0 0 10px;
}
.contacts__link:last-child {
  margin: 0;
}

.courses {
  width: 100%;
  color: orange;
  background: black;
  padding: 50px;
}

.courses__title {
  width: 100%;
  max-width: 1440px;
  text-align: center;
  display: block;
  padding: 0;
  margin: 0 auto 30px;
}

.courses__slider {
  width: 100%;
  max-width: 1440px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0 50px;
  margin: 0 auto;
}

.courses__main-slide {
  width: 100%;
  display: flex;
  align-items: center;
}

.courses__main-slide img {
  width: 612px;
  height: auto;
  margin: 0;
  transition-duration: 0.3s;
}

.courses__main-slide--current img {
  box-shadow: 0 0 10px 5px red;
  transition-duration: 0.7s;
}

.courses__slides {
  display: grid;
  grid-template-columns: 300px 300px;
  gap: 20px 50px;
}

.courses__slides figure {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  padding: 0;
}

.courses__slides img {
  height: auto;
  transition-duration: 0.3s;
}

.courses__slides figure:hover img {
  box-shadow: 0 0 10px 5px red;
  outline: none;
  border: none;
  transition-duration: 0.7s;
}

.education {
  width: 100%;
  max-width: 1440px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: 0 50px 50px;
  margin: 0 auto;
}

.education__title {
  width: 100%;
  text-align: center;
  margin: 0 0 30px;
}

.education__info-container {
  width: 80%;
}

.education__text {
  margin: 0 0 10px;
}

.education__svg {
  width: 20%;
  height: 100px;
}

.education:hover .education__svg-a {
  fill: orange;
}

.education:hover .education__svg-b {
  fill: deepskyblue;
}

.footer {
  width: 100%;
  color: white;
  background: black;
  padding: 50px;
}

.footer__wrapper {
  width: 100%;
  max-width: 1440px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.header {
  width: 100%;
  background: black;
  padding: 50px 0;
}

.header__wrapper {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;
}

.header__list {
  width: 100%;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  margin: 0;
}

.header__item {
  transition-duration: 0.7s;
}

.header__item:hover {
  background-color: deepskyblue;
  border-radius: 10px;
  transition-duration: 0.7s;
}

.header__item:hover .header__link {
  color: black;
}

.header__link {
  text-decoration: none;
  display: block;
  color: #FFFFFF;
  padding: 10px;
}

.hobby {
  width: 100%;
  max-width: 1440px;
  padding: 50px;
  margin: 0 auto;
}

.hobby__title {
  text-align: center;
  margin: 0 0 50px;
}

.hobby__text {
  font-size: 16px;
  line-height: 20px;
  margin: 0 0 10px;
}

.hobby__list {
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 40px 20px;
  padding: 0;
  margin: 0 auto;
}

.hobby__item {
  width: 100%;
  min-height: 130px;
  text-align: center;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  color: black;
  background: white;
  border: 4px solid deepskyblue;
  border-radius: 50px;
  overflow: hidden;
  transition-duration: 0.7s;
}
.hobby__item:hover {
  box-shadow: 0 0 10px 5px currentColor;
  border: none;
  transition-duration: 0.7s;
}

.hobby__item-description {
  width: 80%;
  margin: 0;
}

.hobby__item-svg {
  width: 50px;
  height: 50px;
}

.hobby__button {
  font-family: inherit;
  font-size: 16px;
  line-height: 20px;
  border-radius: 20px;
  background: rgba(114, 158, 134, 0.0295);
  padding: 10px 20px;
  margin: 0;
  cursor: pointer;
}

.photo-gallery {
  width: 100%;
  max-width: 1440px;
  text-align: center;
  color: black;
  background: white;
  padding: 50px;
  margin: 0 auto;
}

.photo-gallery__title {
  margin: 0 0 50px;
}

.photo-gallery__container {
  width: 800px;
  height: 800px;
  display: flex;
  flex-flow: row wrap;
  margin: 0 auto;
}

.photo-gallery__slider {
  height: 400px;
  list-style: none;
  position: relative;
  padding: 0;
  margin: 0 0 20px 0;
  overflow: hidden;
}

.photo-gallery__slide {
  width: 400px;
  height: 400px;
  position: absolute;
  transition: all 0.5s;
}

.photo-gallery__picture,
.photo-gallery__image {
  width: 100%;
  height: 100%;
}

.photo-gallery__button-container {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.photo-gallery__button {
  width: 150px;
  height: 50px;
  font-size: 20px;
  line-height: 22px;
  border: 3px solid deepskyblue;
  border-radius: 20px;
  color: black;
  background-color: #ffffff;
}

.portfolio {
  width: 100%;
  text-align: center;
  color: white;
  background: black;
  padding: 50px;
}

.portfolio__wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.portfolio__title {
  color: orange;
  margin: 0 0 30px;
}

.portfolio__text {
  margin: 0 0 20px;
}

.portfolio__list {
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px 10%;
  padding: 0;
  margin: 0;
}

.portfolio__item {
  width: 100%;
  background: black;
  border: 2px solid deeppink;
  border-radius: 40px;
}

.portfolio__link {
  text-decoration: none;
  text-align: center;
  display: flex;
  flex-flow: row wrap;
  color: inherit;
  padding: 20px;
}

.portfolio__item:hover {
  transform: scale(95%);
  transition: 1s ease;
  border: 2px solid deepskyblue;
}

.portfolio__link-title {
  width: 100%;
  display: block;
  margin: 0 0 20px;
}

.portfolio__picture {
  margin: 0 auto;
}

.portfolio__image {
  width: 100%;
  height: 300px;
  margin: 0 auto;
  object-fit: contain;
}

.portfolio__description {
  margin-bottom: 10px;
}

.portfolio__item-description:last-child {
  margin-bottom: 0;
}

.preview {
  width: 100%;
  max-width: 1440px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
  margin: 0 auto;
}

.preview__title {
  width: 80%;
  font-size: 26px;
  line-height: 30px;
  text-align: center;
  color: orange;
  margin: 0;
}

.skills {
  width: 100%;
  text-align: center;
  color: #241818;
  background: black;
  padding: 50px;
}

.skills__wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.skills__title {
  color: orange;
  margin: 0 0 50px;
}

.skills__text {
  color: orange;
  margin: 0 0 30px;
}

.skills__list {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 20px;
  color: white;
  counter-reset: counter-name;
  padding: 0;
  margin: 0;
}

.skills__item {
  width: 100%;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  text-align: center;
  position: relative;
  border: 1px solid deepskyblue;
  border-radius: 35px;
  padding: 75px 20px 50px;
  box-sizing: border-box;
  overflow: hidden;
}
.skills__item::before {
  counter-increment: counter-name;
  content: "0" counter(counter-name);
  width: 100%;
  height: 55px;
  font-size: 28px;
  line-height: 38px;
  font-weight: 700;
  letter-spacing: -1.27px;
  position: absolute;
  top: 1px;
  left: 50%;
  transform: translateX(-50%);
  color: #0ad9c6;
  padding-top: 25px;
  box-sizing: border-box;
}

@keyframes shake {
  0%, 50%, 100% {
    transform: rotate(0deg);
  }
  10%, 30% {
    transform: rotate(-10deg);
  }
  20%, 40% {
    transform: rotate(10deg);
  }
}
.social__container {
  width: 50%;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
}

.social__link {
  width: 40px;
  height: 40px;
}

.social__vk,
.social__instagram {
  width: 40px;
  height: 40px;
}

.social__vk path,
.social__instagram path {
  width: 40px;
  height: 40px;
  fill: white;
}

.social__vk:hover,
.social__instagram:hover {
  animation: shake 1.2s ease-in-out;
}